import Vue from 'vue'
import Router from 'vue-router'
import Inicio from './views/Inicio.vue'
import Seccion from './views/Seccion.vue'
import Interior from './views/Interior.vue'
import Contacto from './views/Contacto.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path:'/enbreveBack/public/',
      redirect: '/inicio'
    },
    {
      path:'/',
      redirect: '/inicio'
    },
    {
      path: '/inicio',
      name: 'inicio',
      component: Inicio
    },
    {
      path: '/sociales',
      name: 'sociales',
      component: Seccion,
      props:{seccionID:1}
    },
    {
      path: '/cultura',
      name: 'cultura',
      component: Seccion,
      props:{seccionID:2}
    },
    {
      path: '/articulos',
      name: 'artículos',
      component: Seccion,
      props:{seccionID:'articulo'}
    },
    {
      path: '/busqueda',
      name: 'busqueda',
      component: Seccion,
    },
    {
      path: '/interior/:tipoItem/:id',
      name: 'interior',
      component: Interior,
    },
    {
      path: '/contacto',
      name: 'contacto',
      component: Contacto,
    }
  ]
})
