<template>
   <div v-if="ready">
		<banner :central="banners[0].imagen" class="mb"/>
		<seccion v-if="!$route.query.busqueda" :items="principales" :nombre="$route.name" class="mb" :tipoItem="seccionID=='articulo'?'articulo':'noticia'"/>
		<b-container>
			<b-row>
				<b-col lg="9">
					<h5 v-if="this.$route.query.busqueda">
						{{generales.length==0?`No existen resultados para: `:'Resultados encontrados para: '}}{{$route.query.busqueda}}
					</h5>
					<hr v-if="this.$route.query.busqueda"/>
					<b-row>
						<transition-group name="fade" tag="span" class="d-flex flex-wrap w-100">
							<b-col md="6" lg="4" class="mb" v-for="item in generales" :key="item.id">
								<noticia
									ref="noticias"
									:tituloSize="tituloSize"
									:tagSize="tagSize"
									:item="item"
									:tipoItem="seccionID=='articulo'?'articulo':'noticia'"
									resize="300"
								/>
							</b-col>
						</transition-group>
					</b-row>
				</b-col>
				<b-col lg="3">
					<b-row class="flex-lg-column banners">
						<b-col>
							<banner :lateral="banners[1].imagen" class="mb"/>
						</b-col>
							<b-col>
									<banner :lateral="banners[2].imagen" class="mb"/>
							</b-col>
							<b-col>
									<banner :lateral="banners[3].imagen" class="mb"/>
							</b-col>
					</b-row>
				</b-col>
			</b-row>
		</b-container>

		<banner :izquierdo="banners[4].imagen" :derecho="banners[5].imagen" class="mb"/>

		<eb-footer/>

		<eb-terminos/>
   </div>
</template>

<script>
	import axios from 'axios'
	import EbFooter from '../components/Footer'
	import EbTerminos from '../components/Terminos'
	import Banner from '../components/Banner'
	import Noticia from '../components/Noticia'
	import Seccion from '../components/Seccion'
	import {urlApi} from '../urlBase'
	export default {
		components:{
			EbFooter,
			EbTerminos,
			Banner,
			Noticia,
			Seccion
		},
		props:['seccionID'],
		data() {
			return {
				ready:false,
				banners: {},
				principales:[],
				generales:[],
				cantidadNotas:23,
				consultando:true,
				limiteNotas:100,
				pagina:1,
			}
		},
		metaInfo(){
			return {
				title: 'EnBreve',
				titleTemplate: `%s | ${this.$route.name}`,
				meta: [
					{property: 'og:title', content:this.$route.name},
					{property: 'og:site_name', content:'enbreve.info'},
					{property: 'og:type', content: 'website'},
					{property: 'og:url', content: `http://enbreve.info/${this.$route.name}`},
					{property: 'og:image', content:'http://enbreve.info/img/logo.png'},
					{property: 'og:description', content: 'Medio de comunicación en línea con información social y cultural de Xalapa, artículos de interés general y cartelera de eventos.'}
				]
			}
		},
		async mounted(){
			await this.getItems()
			window.onscroll = () =>
			{
				if(this.$refs.noticias && this.$refs.noticias.length>=12 && this.consultando==false){
					let lengthRefs = this.$refs.noticias.length-12
					let element = this.$refs.noticias[lengthRefs].$el
					var y = 0;
					while (element.offsetParent) {
						y += element.offsetTop;
						element = element.offsetParent;
					}
					if(window.scrollY>=y){
						this.consultando = true
						this.getItems()
					}
				}
			}
			await this.getBanners()
			this.ready = true
		},
		beforeDestroy(){
			window.onscroll = null
		},
		methods:{
			async getBanners(){
				let banners = await axios.post(`${urlApi}/getBannersHome`,{
					arregloBanners:[8,9,10,11,12,13],
				})
				this.banners = banners.data
			},
			async getItems(){
				let {data} = await axios.post(`${urlApi}/${this.$route.query.busqueda?'getBusqueda':this.seccionID=='articulo'?'getAllArticulos':'getAllNoticiasSeccion'}`,{
					pagina:this.pagina,
					seccion:this.seccionID,
					busqueda:this.$route.query.busqueda
				})
				if(data.length>0){
					if(this.pagina==1 && !this.$route.query.busqueda){
						this.principales = data.slice(0,8)
						this.generales = data.slice(8,data.length)
					}else{
						this.generales.push(...data)
					}
				}else{
					window.onscroll = null
				}
				this.pagina++
				this.consultando = false
			}

		},
        computed:{
            tagSize(){
                return this.$mq === 'md' || this.$mq==='lg'?'.6rem':'.8rem'
            },
            tituloSize(){
               return this.$mq==='xs'?'1.2rem':this.$mq==='sm'?'1rem':this.$mq==='md'?'.7rem':'.8rem' 
            }
        }
	}
</script>

<style lang="scss" scoped>
	.banners{
		position: -webkit-sticky;
		position: sticky;
		top: 0rem;
	}
	hr{
		background-color: $rojo;
		border: .125rem solid $rojo;
		width: 100%;
		margin-top: .625rem;
    }
</style>

